import React, {useContext, useEffect, useRef, useState} from "react";
import {
    Box,
    Flex,
    Input, InputGroup, InputLeftElement,
    Progress,
    Text
} from "@chakra-ui/react";

import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faEnvelope, faLock} from "@fortawesome/free-solid-svg-icons";
import {isValidEmail} from "../../../common/util/EmailValidator";
import {ProfileContext} from "../../../config/profile/ProfileContext";
import {useAuth} from "../AuthContext";

function AuthModal(props) {

    /**
     *  
     */
    const profile = useContext(ProfileContext);

    /**
     *  <->   
     */
    const [isLoginForm, setIsLoginForm] = useState(true);

    /**
     *  
     */
    const [isLoading, setIsLoading] = useState(false);

    /**
     * ,  
     */
    const idRef = useRef(null);
    const passwordRef = useRef(null);

    /**
     *  
     */
    const {sign} = useAuth();

    function toggleForm() {
        setIsLoginForm(!isLoginForm);
    }

    function validateForm() {
        const email = idRef.current.value;
        const password = passwordRef.current.value;

        if (!isValidEmail(email)) {
            idRef.current.setCustomValidity('   .');
            idRef.current.reportValidity();
            return false;
        }

        if (password.length < 6) {
            passwordRef.current.setCustomValidity('  6  .');
            passwordRef.current.reportValidity();
            return false;
        }

        return true;
    }

    async function signIn() {
        setIsLoading(true);

        const result = await sign(idRef.current.value, passwordRef.current.value, "signIn");

        setIsLoading(false);

        if (result) {
            props.setIsModalOpen(false);
        }
    }

    async function signUp() {
        setIsLoading(true);

        const result = await sign(idRef.current.value, passwordRef.current.value, "signUp");

        setIsLoading(false);

        if (result) {
            props.setIsModalOpen(false);
        }
    }

    const handleIdKeyDown = (event) => {
        idRef.current.setCustomValidity("");

        if (event.key === "Enter") {
            passwordRef.current.focus();
        }
    }

    const handlePasswordDown = (event) => {
        passwordRef.current.setCustomValidity("");

        if (event.key === "Enter") {
            if (validateForm()) {
                isLoginForm ?
                    signIn() : signUp();
            }
        }
    }

    useEffect(() => {
        setIsLoginForm(true);
    }, [props.isOpen]);

    if (!props.isOpen) return null;

    return (
        <Box position="fixed"
             top="10px" right="20px"
             width="300px"
             bg="#22191b" borderRadius="10px"
             pl="15px" pr="15px"
        >
            <Flex direction="column" justify="start" align="stretch" height="100%"
            >
                <Box h="15px"/>
                <Header isLoginForm={isLoginForm} toggleForm={toggleForm} profile={profile}/>
                <Box h="10px"/>
                {isLoading ? <Progress size='xs' isIndeterminate/> : <Progress size='xs' value={0}/>}
                <Box h="10px"/>
                <AuthForm idRef={idRef} passwordRef={passwordRef} onIdKeyDown={handleIdKeyDown}
                          onPasswordKeyDown={handlePasswordDown}
                />
                <Box h="18.5px"/>
            </Flex>
        </Box>
    );
}

function Header(props) {
    return (
        <Flex direction="column">
            <Text fontSize="15px" color="#fde8ed">
                  {props.profile.host} <Text as="span" fontSize="15px" fontWeight="extrabold">
                {props.isLoginForm ? "" : ""}
            </Text>
            </Text>
            <Box h="6.5px"/>
            <Flex direction="row" justify="start" align="start">
                <Text fontSize="12.5px" color="#fde8ed">
                    {props.isLoginForm ? "  ?" : "?"}
                </Text>
                <Box w="10px"/>
                <Text fontSize="12.5px" color="#fde8ed" fontWeight="bold"
                      cursor="pointer" _hover={{color: "#fde8ed"}}
                      onClick={props.toggleForm}
                >
                    {props.isLoginForm ? "" : ""}
                </Text>
            </Flex>
        </Flex>
    );
}


function AuthForm({idRef, passwordRef, onIdKeyDown, onPasswordKeyDown}) {
    return (
        <Flex direction={"column"}>
            <InputGroup>
                <InputLeftElement
                    children={<FontAwesomeIcon icon={faEnvelope} color="#fde8ed"/>}
                />
                <Input ref={idRef} color="#fde8ed"
                       placeholder="" _placeholder={{color: "#fde8ed"}}
                       onKeyDown={onIdKeyDown}
                />
            </InputGroup>
            <Box h="10px"/>
            <InputGroup>
                <InputLeftElement
                    children={<FontAwesomeIcon icon={faLock} color="#fde8ed"/>}
                />
                <Input type={"password"} ref={passwordRef} color="#fde8ed"
                       placeholder="" _placeholder={{color: "#fde8ed"}}
                       onKeyDown={onPasswordKeyDown}
                />
            </InputGroup>
        </Flex>
    );
}

export default AuthModal;